body {
  margin: 0;
  padding: 0;
  background-color: #37322d;
  font-family: 'Seravek', 'Calibri', -apple-system, BlinkMacSystemFont, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 1em;
  font-weight: 400;
  color: #c3c8c8;
}
h1,
h2,
h3 {
  font-size: 0.875em;
  font-weight: 400;
  color: #e1dcd7;
}
h1 {
  margin: 8px;
  text-align: center;
}
p {
  font-size: 0.875em;
  font-weight: 400;
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  font-size: 0.875em;
  cursor: pointer;
}
.navigator {
  position: fixed;
  width: 100vw;
  height: 36px;
  bottom: 0;
  left: 0;
  background-color: rgba(50, 50, 45, 0.5);
}
.navigator .navigator_content_wrapper {
  height: inherit;
  width: inherit;
  display: flex;
  justify-content: space-between;
}
.navigator .navigator_content_wrapper .navs {
  display: flex;
  justify-content: flex-start;
  height: 32px;
  max-width: 50%;
  overflow: hidden;
  margin: 2px;
  cursor: pointer;
}
.navigator .navigator_content_wrapper .tools {
  display: flex;
  justify-content: space-around;
  height: 32px;
  width: 50%;
  margin: 2px;
}
.navigator .navigator_content_wrapper .icon {
  cursor: pointer;
}
.navigator .navigator_content_wrapper .disabled {
  opacity: 0.125;
  cursor: not-allowed;
}
.navigator .navigator_content_wrapper .gallery-name {
  height: 32px;
  line-height: 32px;
  font-size: 0.875em;
  font-weight: 600;
}
.gallery-list li {
  margin: 8px;
}
.gallery-list li .title {
  color: #e1dcd7;
  text-align: center;
  height: 20px;
  line-height: 20px;
}
.photo {
  max-width: 100%;
  display: block;
  position: relative;
  outline: 1px solid #e1dcd7;
  outline-offset: -1px;
  border: 2px solid rgba(50, 50, 45, 0.5);
  box-sizing: border-box;
}
.caught {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
.single_photo {
  position: fixed;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  text-align: center;
  overflow: hidden;
}
.single_photo .single_slide {
  position: relative;
}
.single_photo .single_slide .photo_wrapper {
  position: relative;
  margin: 16px;
  padding-bottom: 36px;
}
.single_photo .single_slide .photo_wrapper .photo {
  margin: 0 auto;
  box-shadow: 0 16px 32px rgba(5, 5, 0, 0.25);
  max-height: calc(100vh - 68px);
}
.single_photo .single_slide p {
  margin: 8px 0;
}
.single-gallery {
  text-align: center;
  margin: 8px 4px 44px 4px;
}
.single-gallery ul {
  min-height: 100vh;
}
.single-gallery ul li {
  position: relative;
}
.single-gallery ul li .photo {
  margin: 8px auto;
  box-shadow: 0 4px 8px rgba(50, 50, 45, 0.5);
}
.single-gallery ul li .photo_title {
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
  height: 36px;
  margin: 0 60px;
  line-height: 36px;
  font-size: 0.875em;
  color: #e1dcd7;
  z-index: 3;
}
.icon {
  width: 32px;
  height: 32px;
  background-position: center center;
  background-size: 24px 24px;
