.logo {
  background-image: url(data:image/svg+xml;base64,);
  width: 22px;
  height: 32px;
  background-position: 0 6px;
  background-size: 21px 21px;
  background-repeat: no-repeat;
  opacity: 0.875;
}
.main-logo {
  background-size: 42px 42px;
  background-position: center center;
  width: 44px;
  height: 44px;
  margin: 8px auto 0 auto;
}
.invert {
  -webkit-filter: invert(100%);
          filter: invert(100%);
}
.king-in-the-north {
  position: relative;
  height: 200px;
  width: 200px;
  margin: 8px auto;
}
.king-in-the-north .stark {
  display: block;
  outline: 2px solid #e1dcd7;
  outline-offset: -1px;
  box-shadow: 0 4px 8px rgba(5, 5, 0, 0.25);
  height: 160px;
  width: 160px;
  position: absolute;
  top: 20px;
  left: 20px;
  object-fit: cover;
  transition: all 0.05s ease;
  border: 2px solid rgba(50, 50, 45, 0.5);
  box-sizing: border-box;
}
.apply_wrapper .apply_button {
  display: block;
  text-align: center;
  margin: 16px auto;
  height: 30px;
  line-height: 30px;
  border-radius: 15px;
  width: 120px;
  box-shadow: 0 4px 8px rgba(5, 5, 0, 0.25);
  font-size: 0.875em;
  font-weight: 500;
  color: #c3c8c8;
  background: linear-gradient(270deg, #ff7e00, #0080ff);
  background-size: 200% 200%;
}
.apply_wrapper .apply_button.apply_yellow {
  -webkit-animation: DaNang 4s ease infinite;
          animation: DaNang 4s ease infinite;
}
.apply_wrapper .apply_button.apply_blue {
  -webkit-animation: DaNang 4s ease infinite;
          animation: DaNang 4s ease infinite;
}
.apply_wrapper .apply_button.apply_crazy {
  -webkit-animation: DaNang 1s ease infinite;
          animation: DaNang 1s ease infinite;
}
.apply_wrapper .apply_content {
  text-align: start;
  text-indent: 1em;
  margin: 16px 4px;
  background: linear-gradient(270deg, #ff7e00, #0080ff);
  background-size: 200% 200%;
  -webkit-animation: DaNang 1s ease infinite;
          animation: DaNang 1s ease infinite;
  overflow: hidden;
  border-radius: 4px;
}
.apply_wrapper .apply_content .apply_content_inner {
  margin: 1px;
  padding: 8px;
  background-color: #37322d;
  border-radius: 3px;
}
.apply_wrapper .apply_memo p {
  margin: 0;
  line-height: 1.5;
  font-size: 0.75em;
  color: #c3c8c8;
}
.alien {
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  overflow: hidden;
  align-items: center;
  justify-content: center;
}
.alien .keyboard {
  width: 300px;
  height: 300px;
  display: flex;
  flex-wrap: wrap;
}
.alien .keyboard .key {
  width: 90px;
  height: 90px;
  margin: 5px;
  background-color: rgba(0, 0, 0, 0.25);
  transition: 0.125s ease;
  cursor: pointer;
}
.alien .keyboard .key .icon {
  opacity: 0.5;
  transition: inherit;
  margin: 28px;
}
.alien .keyboard .key:active {
  background-color: rgba(0, 0, 0, 0.125);
}
.alien .keyboard .key:active .icon {
  opacity: 0.25;
}
@-webkit-keyframes DaNang {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes DaNang {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.trans_blur-enter-active,
.trans_blur-leave-active {
  transition: 0.5s;
}
.trans_blur-enter,
.trans_blur-leave-to {
  -webkit-filter: blur(10px);
          filter: blur(10px);
  opacity: 0;
}
